import React from 'react';

import styles from './Project.module.css';
import data from '../../assets/data.json';
import images from '../../assets/img/images';
import Loader from '../Loading/Loader';
import Img from 'react-image';

function Project (props) {
    const site = props.match.params.id;
    return (
        <div className={styles.container}>
            { data[site].title !== undefined &&
                <div className={styles.sub}>
                    <div className={styles.box1}>
                        <Img src={images[data[site].clip].default} loader={<Loader />} alt={data[site].title} />
                    </div>
                    <div className={styles.box2}>
                        <h1>Project : {data[site].title}</h1>
                        <p>Description: {data[site].description}</p>
                        { data[site].detail && <p className={styles.detail}>Detail: {data[site].detail}</p> }
                        <p>
                            <br></br>
                            { data[site].link ? <a href={data[site].link} target="_blank" rel="noopener noreferrer">{data[site].link}</a> : 'no link yet... ¯\\_( ツ )_/¯'}
                        </p>
                    </div>
                </div>
            }
        </div>
    );
}

export default Project;
