import React from 'react';

import styles from './about.module.css';
import profil from '../../assets/img/profile_n_b.jpg';

function About() {
    function getAge(date) { 
        var diff = Date.now() - date.getTime();
        var age = new Date(diff); 
        return Math.abs(age.getUTCFullYear() - 1970);
    }
	return (
        <div className={styles.container}>
            <div className={styles.box}>
                <div className={styles.content}>
                    <img src={profil} alt="profil" />
                    <h1>Jason</h1>
                    <h2>Age {getAge(new Date(1995, 7, 16))}</h2>
                    <h2>French / Filipino</h2>
                    <h2>Web developper / Motion designer / Video editor</h2>
                </div>
            </div>
            <div className={styles.box}>
                <div className={styles.content}>
                    <h1>About me</h1>
                    <p className={styles.intro}>Hello, I'm a Jason.<br />I'm self-taught (web) developper, video editor, motion designer and still learning.</p>
                    <div><p><b>Skills</b>:</p>
                        <ul>
                            <li>
                                <p>- <b>C</b> <small>(language)</small></p></li>
                                <div className={styles.progress}>
                                    <div className={styles.high}></div>
                                </div>
                            <li>
                                <p>- <b>PHP</b>:</p>
                                <div className={styles.progress}>
                                    <div className={styles.low}></div>
                                </div>
                                <p><small>PHPmyAdmin, Mysql, Symfony,  Laravel, Twig, Blade</small></p>
                            </li>
                            <li>
                                <p>- <b>JAVASCRIPT</b>:</p>
                                <div className={styles.progress}>
                                    <div className={styles.medium}></div>
                                </div>
                                <p><small>Jquery, React</small></p>
                            </li>
                            <li><p>- <b>Html</b> / <b>CSS (<small>Bootstrap</small>)</b> / <b>Git</b> (<small>Github</small>)</p></li>
                            <li>
                                <p>- <b>After Effects</b></p>
                                <div className={styles.progress}>
                                    <div className={styles.high}></div>
                                </div>
                            </li>
                            <li>
                                <p>- <b>Photoshop</b></p>
                                <div className={styles.progress}>
                                    <div className={styles.high}></div>
                                </div>
                            </li>
                            <li>
                                <p>- <b>Premier pro</b></p>
                                <div className={styles.progress}>
                                    <div className={styles.high}></div>
                                </div>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
	);
}

export default About;